@font-face {
  font-family: "MontserratExtraLight";
  src: url("./fonts/Montserrat-ExtraLight.woff2") format("woff2"),
       url("./fonts/Montserrat-ExtraLight.otf") format("otf");
}

@font-face {
  font-family: "PetitFormalScript";
  src: url("./fonts/PetitFormalScript-Regular.woff2") format("woff2"),
       url("./fonts/PetitFormalScript-Regular.ttf") format("ttf");
}

@font-face {
  font-family: "Playfair";
  src: url("./fonts/PlayfairDisplay-VariableFont_wght.woff2") format("woff2"),
       url("./fonts/PlayfairDisplay-VariableFont_wght.ttf") format("ttf");
}

.App-loading {
  text-align: center;
  max-width: 480px;
  margin: 0 auto;
}


.App {
  text-align: center;
  max-width: 480px;
  margin: 0 auto;
  opacity: 0;
}

.App.fadeIn {
  transition: opacity 1.3s;
  opacity: 1;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.enter-button{
  color: #706F6F;
  border:  1px solid #706F6F;
  background-color: transparent;
  padding: 2px 16px 4px 16px;
  border-radius: 12px;
  font-family: 'Playfair';
  font-size: 12px;
}

.playsong {
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 18px;
  padding: 5px 0 0 5px;
}
.playsong.playing {
  padding: 3px 0 0 1px;
}

.input-confirm {
  background-color: transparent;
  color: #FFFFFF;
  border: none;
  border-bottom: 1px solid rgba(255,255,255,0.4);
  padding: 3px 0;
}
.input-confirm:focus-visible {
  outline: none;
  border-bottom: 2px solid #FFFFFF;
}

.confirm-button{
  color: #FFFFFF;
  border:  1px solid #FFFFFF;
  background-color: transparent;
  padding: 2px 16px;
  border-radius: 11px;
  font-family: 'Playfair';
  font-size: 10px;
}

.confirm-button:active{
  background-color: rgba(255,255,255,0.3);
}
